:root {
  --bg-color: #0a1f44;      /* bleu foncé par défaut */
  --text-color: #fff;        /* texte blanc par défaut */
  --btn-bg-color: #00bfff;   /* bouton bleu ciel */
  --btn-text-color: #fff;    /* texte bouton */
}

body, main, section,div {
  background-color: var(--bg-color);
  color: var(--text-color);
  transition: 0.3s;
  font-family: cursive;
}

button {
  color: var(--btn-text-color);
  transition: 0.3s;
  padding: 10px 20px;
  font-weight: bold;
  cursor: pointer;
  text-decoration: none;
}
a{
  color: grey;
  transition: 0.3s;
  padding: 10px 20px;
  font-weight: bold;
  cursor: pointer;
  text-decoration: none;
}

a:hover{
  color: #00bfff;
  
}

/* globals.css */

/* Section principale */
.section-content {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 0 20px;
  transition: 0.3s;
}

/* Titres */
.section-content h1 {
  font-size: 3rem;
  margin-bottom: 20px;
}

.section-content p {
  font-size: 1.2rem;
  margin-bottom: 30px;
}

/* Bouton CV */
.cv-button {
  padding: 12px 25px;
  background-color: var(--btn-bg-color);
  color: var(--btn-text-color);
  border-radius: 20px;
  font-weight: bold;
  text-decoration: none;
  transition: 0.3s;
  box-shadow: 0 4px 8px rgba(0,0,0,0.2);
}

/* Responsive pour mobile / tablette */
@media (max-width: 768px) {
  .section-content h1 {
    font-size: 2rem;
  }
  .section-content p {
    font-size: 1rem;
  }
  .cv-button {
    padding: 10px 20px;
  }
}

